import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { ReactComponent as ArrowLeft } from '../assets/arrow-back.svg';

const NotePage = () => {
  let { id } = useParams();
  let history = useNavigate();
  let [note, setNote] = useState({ title: '', content: '' });

  useEffect(() => {
    const getNote = async () => {
      try {
        if (id === 'new') return
        let response = await fetch(`/notes/${id}/`);
        if (response.ok) {
          let data = await response.json();
          setNote(data);
        } else {
          console.error(`Failed to fetch note with ID ${id}`);
        }
      } catch (error) {
        console.error('Error fetching note:', error);
      }
    };

    getNote();
  }, [id]);

  let createNote = async () => {
    fetch(`/notes/`, {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(note)
    });
  };

  let updateNote = async () => {
    fetch(`/notes/${id}/`, {
      method: "PUT",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(note)
    });
  };

  let deleteNote = async () => {
    fetch(`/notes/${id}/`, {
      method: 'DELETE',
      headers:{
        'Content-Type': 'application/json'
      }
    })
    history('/')
  }

  let handleSubmit = async () => {
    if(id !== 'new' && (note.content || note.title) === ''){
      deleteNote()
    }else if (id !== 'new'){
      await updateNote();
    }else if(id === 'new' && (note.title && note.content) !== null)
      createNote()
    history("/");
  };

  return (
    <div className='note'>
      <div className='note-header'>
        <h3>
            <ArrowLeft onClick={handleSubmit} />
        </h3>
        {id !== 'new' ? (
          <button onClick={deleteNote}>Delete</button>
        ): (
          <button onClick={handleSubmit}>Done</button>
        )
      }
      </div>
      <input
        type="text"
        placeholder="Note Title"
        value={note.title}
        onChange={(e) => {
          setNote({ ...note, title: e.target.value });
        }}
      />
      <textarea
        placeholder="Note Content"
        value={note.content}
        onChange={(e) => {
          setNote({ ...note, content: e.target.value });
        }}
      ></textarea>
    </div>
  );
};

export default NotePage;
